*,
::before,
::after {
  box-sizing: border-box;
}

body {
  position: relative;
  margin: 0;
  overflow: hidden;
  display: flex;
  height: 100vh;
  flex-direction: column;
  line-height: 1.5;
  font-family: tahoma;
  color: #f4f4f4;
  font-size: 59.8%;
  background-color: #1d1e22;
}

.smooth {
  position: relative;
  width: 100%;
  height: 100%;
  scroll-behavior: smooth;
  overflow-y: hidden;
  scroll-snap-type: mandatory;
  scroll-snap-points-y: repeat(100vh);
  scroll-snap-type: y mandatory;
}

.header {
  position: fixed;
  top: 0;
  z-index: 1;
  display: flex;
  width: 100%;
  min-height: 59px;
  font-size: 1rem;
  background: linear-gradient(
    180deg,
    #1d1e22,
    rgba(29, 30, 34, 0.1),
    transparent
  );
  mix-blend-mode: luminosity;
  ul {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    width: 100%;
    justify-content: center;
    li a {
      position: relative;
      text-decoration: none;
      text-transform: uppercase;
      font-weight: 590;
      color: #f4f4f4;
      margin-right: 15px;
      &::after {
        position: absolute;
        bottom: -3px;
        left: 0;
        content: "";
        width: 0;
        height: 3px;
        transition: all 0.2s linear;
      }
      &:hover::after {
        width: 100%;
        background-color: #f4f4f4;
      }
    }
  }
}

section {
  position: relative;
  display: flex;
  width: 100%;
  height: 100vh;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  background-color: #f4f4f4;
  background-size: cover;
  scroll-snap-align: center;
  &:nth-child(2) {
    background-image: url("/assets/images/1.jpg");
  }
  &:nth-child(3) {
    background-image: url("/assets/images/2.jpg");
  }
  &:nth-child(4) {
    background-image: url("/assets/images/3.jpg");
  }
  &:nth-child(5) {
    background-image: url("/assets/images/4.jpg");
  }
  h2 {
    text-transform: uppercase;
    font-size: 3.25rem;
    font-weight: 900;
    padding: 0.5em 1em;
    color: #1d1e22;
    background-color: #f4f4f4;
    mix-blend-mode: screen;
    border-radius: 0.25em;
    pointer-events: none;
    user-select: none;
    opacity: 0.6;
  }
}
